<template>
  <div>
    <div class="m-container">
      <view-header title="排班管理"></view-header>
      <div class="m-table-div">
		<el-table
			:data="list"
			tooltip-effect="dark"
			border
			stripe
			style="width: 100%">
			<!-- <el-table-column type="index" label="序号" align="center" width=""></el-table-column> -->
			<el-table-column prop="clinicDate" label="日期" :formatter="formatDate" header-align="center" align="center" ></el-table-column>
			<el-table-column prop="doctorName" label="专家名称" header-align="center" align="center"></el-table-column>
			<el-table-column prop="clinicTimeMorning" label="上午" header-align="center" align="center" width="150"></el-table-column>
			<el-table-column prop="clinicTimeAfternoon" label="下午" header-align="center" align="center" width="150"></el-table-column>
			<el-table-column prop="morningLeft" label="上午余号" header-align="center" align="center"></el-table-column>
			<el-table-column prop="afternoonLeft" label="下午余号" header-align="center" align="center"></el-table-column>
			<el-table-column label="是否停诊" align="center" width="">
			  <template slot-scope="scope">
				<el-switch
				  v-model="scope.row.clinicStatus"
				  active-color="#71D1CB"
				  :active-value="true"
				  :inactive-value="false"
				  inactive-color="#E2E2E2"
				  @change="changeSwitch(scope.row)">
				</el-switch>
			  </template>
			</el-table-column>
	</el-table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
		  @size-change="_init_data"
          @current-change="_init_data"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {querySchedule,formatDate,updateSchedule} from '@/api'
export default {
  created: function(){
	this._init_data();
  },
  components: {
    ViewHeader
  },
  filters: {
    formatDate (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd')
    },
    formatDate2 (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
    }
  },
  methods: {
	_init_data() {
		var _this = this
		var params = new URLSearchParams()
		params.append('currentPage', this.pageNo)
		params.append('pageSize', this.pageSize)
		querySchedule(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this.list = data.data
          _this.total = data.total
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
	},
	changeSwitch (info) {
	  console.log(params)
	  var params = new URLSearchParams()
	  params.append('clinicDate', info.clinicDate)
	  params.append('clinicStatus', info.clinicStatus)
	  updateSchedule(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
		  alert(data.data)
		}
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
	},
	formatDate(row, column) {
	  var time = row[column.property];
	  if (time == undefined) {  
             return "";
      }
	  var date = new Date(time)
	  return formatDate(date, 'yyyy-MM-dd')
	}
  },
  data () {
    return {
	  out:'1',
	  clinic_date:'',
	  doctor_name:'',
	  clinic_time_morning:'',
	  clinic_time_afternoon:'',
	  morning_left:'',
	  afternoon_left:'',
	  clinic_status:'',
      pageSizes: [100, 200, 300, 400],
      pageSize: 100,
      total: 0,
      pageNo: 1,
      list: null
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
.green
  color: $color-back-green_0
</style>


